<template>
  <Dropdown :class="dropdownClass" placement="right-start">
    <li v-if="topLevel" :class="menuItemClass">
      <MenuSideTitle :menu="menu" collapse hideTitle :iconSize="20" />
    </li>
    <DropdownItem v-else>
      <MenuSideTitle :menu="menu" collapse :selected="routeMatched" />
      <Icon type="ios-arrow-forward" />
    </DropdownItem>
    <template #list>
      <DropdownMenu>
        <template v-for="item in menu.children">
          <MenuSideCollapse v-if="item.children && item.children.length" :menu="item" :key="item.path" />
          <a v-else class="ivu-link ivu-link-color" :href="linkUrl(item.path)" :target="item.meta.target" :key="item.path">
            <DropdownItem :class="item.path === $route.path && `${prefixCls}-side-collapsed-item-selected`">
              <MenuSideTitle :menu="item" collapse />
            </DropdownItem>
          </a>
        </template>
      </DropdownMenu>
    </template>
  </Dropdown>
</template>

<script>
  import mixins from './mixins'
  import MenuSideTitle from './MenuSideTitle'

  const LAYOUT_THEME = {
    primary: 'primary',
    light: 'light',
    dark: 'dark'
  }

  export default {
    name: 'MenuSideCollapse',
    mixins: [mixins],
    components: { MenuSideTitle },
    props: {
      menu: {
        type: Object,
        default: () => {}
      },
      theme: {
        type: String,
        default: 'light'
      },
      topLevel: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      dropdownClass() {
        return [
          `${this.prefixCls}-side-collapsed`,
          {
            [`${this.prefixCls}-side-collapsed-dark`]: this.theme === LAYOUT_THEME.dark
          }
        ]
      },
      menuItemClass() {
        return [
          'ivu-menu-item',
          `${this.prefixCls}-side-collapsed-item`,
          {
            'ivu-menu-item-active': this.routeMatched,
            'ivu-menu-item-selected': this.routeMatched
          }
        ]
      }
    }
  }
</script>
