<template>
  <Submenu :name="menu.path">
    <template #title>
      <MenuSideTitle :menu="menu" />
    </template>
    <template v-for="item in menu.children">
      <MenuSideSubmenu v-if="item.children && item.children.length" :menu="item" :key="item.path" />
      <MenuSideItem v-else :menu="item" :key="item.path" />
    </template>
  </Submenu>
</template>

<script>
  import MenuSideItem from './MenuSideItem'
  import MenuSideTitle from './MenuSideTitle'

  export default {
    name: 'MenuSideSubmenu',
    components: {
      MenuSideItem,
      MenuSideTitle
    },
    props: {
      menu: {
        type: Object,
        default: () => {}
      }
    }
  }
</script>
