<template>
  <MenuItem :class="menuItemClasses" :name="menu.path" :to="menu.path" :target="menu.meta && menu.meta.target">
    <MenuSideTitle :menu="menu" :iconSize="iconSize" :hideTitle="hideTitle" :collapse="collapsed" />
  </MenuItem>
</template>

<script>
  import mixins from './mixins'
  import MenuSideTitle from './MenuSideTitle'

  export default {
    name: 'MenuSideItem',
    mixins: [mixins],
    components: { MenuSideTitle },
    props: {
      menu: {
        type: Object,
        default: () => {}
      },
      hideTitle: {
        type: Boolean,
        default: false
      },
      collapsed: {
        type: Boolean,
        default: false
      },
      iconSize: {
        type: Number,
        default: 16
      }
    },
    computed: {
      menuItemClasses() {
        const { prefixCls, collapsed } = this
        return [
          {
            'ivu-menu-item-active': this.routeMatched,
            'ivu-menu-item-selected': this.routeMatched,
            [`${prefixCls}-side-collapsed-item`]: collapsed
          }
        ]
      }
    }
  }
</script>
