<template>
    <Card class="card" :padding="0">
        <div class="card-left">
            <div class="card-left-header">
                <div class="card-left-title">智能编辑器</div>
                <div class="card-left-subtitle">开启快速编辑，AI协助</div>
            </div>
            <div class="card-left-content">
                <Row :gutter="80">
                    <Col :lg="8" :md="8" :sm="24" :xs="24" v-for="(item,index) in modules" :key="index">
                        <Card @click="handleClick(item)">
                            <div>
                                <div v-bg-color="item.bgColor" class="icon-container">
                                    <Icon v-color="item.color" :type="item.icon"/>
                                </div>
                                <span>{{item.text}}</span>
                                <Icon type="md-arrow-forward"/>
                            </div>
                        </Card>
                    </Col>
                </Row>
            </div>
        </div>

    </Card>
</template>

<script>

export default {
  name: 'Home',
  data(){
    return {
      modules:[
        {
          path: '/common',
          color: '#8D55E9',
          bgColor: '#E7E0F4',
          text: '智能润色',
          icon: 'ios-image'
        },
        {
          path: '/common',
          color: '#3E90EF',
          bgColor: '#CBE5FD',
          text: '格式排版',
          icon: 'logo-buffer'
        },
        {
          path: '',
          color: '#3E90EF',
          bgColor: '#CBE5FD',
          text: '思维导图',
          icon: 'logo-buffer'
        },
        {
          path: '/common',
          color: '#51B968',
          bgColor: '#DBF0E0',
          text: '代码解释',
          icon: 'logo-codepen'
        },
        {
          path: '',
          color: '#51B968',
          bgColor: '#DBF0E0',
          text: '代码生成',
          icon: 'md-cube'
        },
        {
          path: '/speech',
          color: '#D74C10',
          bgColor: '#F6DACF',
          text: '语音识别',
          icon: 'ios-mic'
        },
        {
          path: '/editImage',
          color: '#8D55E9',
          bgColor: '#E7E0F4',
          text: '图像编辑',
          icon: 'ios-images'
        },
        {
          path: '',
          color: '#E6891D',
          bgColor: '#FDEDDA',
          text: '语音生成',
          icon: 'ios-musical-notes'
        },
        {
          path: '',
          color: '#3E90EF',
          bgColor: '#CBE5FD',
          text: '教育反馈',
          icon: 'ios-paper'
        },
      ]
    }
  },
  methods: {
    handleClick(item) {
      if (!item.path) {
        this.$Message.warning('敬请期待！')
        return
      }
      this.$store.commit('setCurrentModule', item)
      this.$router.push({ path: item.path })
    }
  }
}
</script>
<style lang="less">
    .editor-middle {

        > .ivu-card {
            height: 100%;
            border-radius: 20px;
        }

        > .ivu-card > .ivu-card-body {
            display: flex;
            height: 100%;
            overflow: hidden;

            .card-left {
                padding: 24px;
                flex: 1;

                &-header {
                    text-align: center;
                    padding: 16px 0 48px 0;
                }

                &-title {
                    color: rgba(0, 0, 0, 0.85);
                    font-weight: 600;
                    font-size: 26px;
                }

                &-subtitle {
                    color: #808695;
                }

                &-content {
                    padding: 0 80px;
                    height: calc(100vh - 208px);
                    overflow: auto;

                    .ivu-card {
                        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
                        height: 150px;
                        margin-bottom: 36px;
                        cursor: pointer;
                        border-radius: 16px;
                        user-select: none;
                    }

                    .ivu-card:hover{
                        box-shadow: 0 3px 6px -4px #0000001f, 0 6px 16px #00000014, 0 9px 28px 8px #0000000d;
                        border-color: #eee;
                    }

                    .ivu-card-body {
                        height: 100%;

                        & > div {
                            display: flex;
                            justify-content: space-evenly;
                            align-items: center;
                            height: 100%;
                            font-size: 16px;
                            font-weight: 500;

                            .icon-container{
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                height: 48px;
                                width: 48px;
                                border-radius: 12px;
                                .ivu-icon{
                                    font-size: 32px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
</style>
