<template>
  <Menu
    ref="menu"
    width="auto"
    :class="[`${prefixCls}-side`, collapsed && `${prefixCls}-side-collapsed`]"
    :theme="theme"
    :accordion="accordion"
    :openNames="openNames"
    :active-name="activeName"
    @on-select="handleSelect"
  >
    <template v-for="menu in menus">
      <template v-if="collapsed">
        <MenuSideCollapse v-if="menu.children && menu.children.length" topLevel :menu="menu" :theme="theme" :key="menu.path" />
        <Tooltip v-else :key="menu.path" :content="menu.meta.title" :theme="theme" placement="right" transfer>
          <MenuSideItem :menu="menuIcon(menu)" hideTitle :iconSize="20" :collapsed="collapsed" />
        </Tooltip>
      </template>
      <template v-else>
        <MenuSideSubmenu v-if="menu.children && menu.children.length" :menu="menu" :key="menu.path" />
        <MenuSideItem v-else :menu="menu" :key="menu.path" />
      </template>
    </template>
  </Menu>
</template>
<script>
  import mixins from './mixins'
  import MenuSideItem from './MenuSideItem'
  import MenuSideSubmenu from './MenuSideSubmenu'
  import MenuSideCollapse from './MenuSideCollapse'

  export default {
    name: 'MenuSide',
    mixins: [mixins],
    components: {
      MenuSideItem,
      MenuSideSubmenu,
      MenuSideCollapse
    },
    props: {
      menus: {
        type: Array,
        required: true
      },
      activeName: {
        type: String,
        required: true
      },
      collapsed: {
        type: Boolean
      },
      theme: {
        type: String,
        default: 'light'
      },
      accordion: {
        type: Boolean,
        default: true
      },
      rootIconSize: {
        type: Number,
        default: 20
      },
      iconSize: {
        type: Number,
        default: 16
      }
    },
    provide() {
      return {
        prefixCls: this.prefixCls
      }
    },
    data() {
      return {
        openNames: []
      }
    },
    watch: {
      activeName(path) {
        if (this.accordion) {
          this.openNames = this.getOpenedNames(path)
        } else {
          this.openNames = this.getUnion(this.openNames, this.getOpenedNames(path))
        }
      },
      menus() {
        this.updateSideMenu()
      },
      collapsed(newVal) {
        if (!newVal) this.updateSideMenu()
      },
      openNames() {
        this.updateSideMenu()
      }
    },
    created() {
      this.openNames = this.getUnion(this.openNames, this.getOpenedNames(undefined))
    },
    methods: {
      menuIcon(menu) {
        return menu.meta.icon ? menu : { ...menu, meta: { ...menu.meta, icon: 'md-apps' } }
      },
      getUnion(arr1, arr2) {
        return Array.from(new Set([...arr1, ...arr2]))
      },
      getOpenedNames(path) {
        return this.$route.matched.map(item => item.path).filter(item => item !== path)
      },
      updateSideMenu() {
        this.$nextTick(() => {
          this.$refs.menu?.updateOpened()
          this.$refs.menu?.updateActiveName()
        })
      },
      handleSelect(path) {
        this.$emit('on-select', path)
      }
    }
  }
</script>
