import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/views/Home.vue'
import Speech from '@/views/Speech.vue'
import EditImage from '@/views/EditImage.vue'
import CommonView from '@/views/Common.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/speech',
    name: 'Speech',
    component: Speech
  },
  {
    path: '/editImage',
    name: 'EditImage',
    component: EditImage
  },
  {
    path: '/common',
    name: 'Common',
    component: CommonView
  }
]

const router = createRouter({
  routes,
  history: createWebHistory(process.env.BASE_URL),
  scrollBehavior() {
    return { top: 0 }
  }
})

export default router
