<template>
  <span :class="sideClass">
    <span v-if="menu.meta.icon" :class="iconClass">
      <Icon :type="menu.meta.icon" :size="iconSize" />
    </span>
    <span v-if="!hideTitle" :class="textClass">
      <span>{{ menu.meta.title || '未命名' }}</span>
      <em v-if="menu.meta.subtitle">{{ menu.meta.subtitle }}</em>
      <!--<Badge v-if="badgeData" v-bind="badgeData" :class="`${prefixCls}-side-badge`" />-->
    </span>
  </span>
</template>

<script>
  import mixins from './mixins'

  export default {
    name: 'MenuSideTitle',
    mixins: [mixins],
    props: {
      menu: {
        type: Object,
        default: () => {}
      },
      hideTitle: {
        type: Boolean,
        default: false
      },
      selected: {
        type: Boolean,
        default: false
      },
      collapse: {
        type: Boolean,
        default: false
      },
      iconSize: {
        type: Number,
        default: 16
      }
    },
    computed: {
      sideClass() {
        const { prefixCls } = this
        return [
          `${prefixCls}-side-title`,
          {
            [`${prefixCls}-side-title-with-collapse`]: this.collapse,
            [`${prefixCls}-side-title-with-subtitle`]: this.menu.meta && this.menu.meta.subtitle,
            [`${prefixCls}-side-title-with-icon`]: this.menu.meta.icon
          }
        ]
      },
      iconClass() {
        const { prefixCls } = this
        return [
          `${prefixCls}-side-title-icon`,
          {
            [`${prefixCls}-side-title-icon-single`]: this.hideTitle,
            [`${prefixCls}-side-title-selected`]: this.selected
          }
        ]
      },
      textClass() {
        const { prefixCls } = this
        return [
          `${prefixCls}-side-title-text`,
          {
            [`${prefixCls}-side-title-selected`]: this.selected
          }
        ]
      }
    }
  }
</script>
